.checkedinner > div{
  display: inline-flex;
  align-items: center;
}
.checkedinner input[type="checkbox"] {
  -webkit-appearance: none;
  position: relative;
  width: 18px;
  height: 18px;
  cursor: pointer;
  outline: none !important;
  border: 1px solid #eaeaea;
  border-radius: 5px;
  background: #fff;
}
.checkedinner label{
  padding-right: 10px;
  display: inline-block;
}
.checkedinner input[type="checkbox"]::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: hidden;
  width:20px;
  height:20px;
  transform: scale(0) translate(-50%, -50%);
  line-height: 1;
}

.checkedinner input[type="checkbox"]:checked {
  background-color: #EB1918;
  border-color: rgba(255, 255, 255, 0.3);
  color: white;
}

.checkedinner input[type="checkbox"]:checked::before, .checkedinner input[type="checkbox"]:checked::after {
  content: ''; 
  display: inline-block; 
  position: absolute; 
  transform-origin: left top; 
  background-color: #fff;
}

.checkedinner input[type="checkbox"]:checked::before {
  border: none;
  top: 8px; 
  left: 2px; 
  width: 2px;
  height: 7px; 
  transform: rotate(-45deg); 
}
.checkedinner input[type="checkbox"]:checked::after {
  top: 13px; 
  left: 8px; 
  width: 2px;
  height: 12px; 
  transform: rotate(-145deg); 
}
