            border-right:1px solid #D0CBC1;
          }
        }
      }
    }
    > .accordion_section {
      position: relative;
      &:nth-child(even){
        > .accordion_header{
          ul{
            background-color: #f5f5f5;
          }
        }
      }
      &::after{
        content: "";
        position: absolute;
        width: 8px;
        height: 8px;
        right:20px;
        top:50%;
        background-color: transparent;
        border-top: solid 2px #4d4d4e;
        border-right: solid 2px #4d4d4e;
        transform: translateY(-50%) rotate(-225deg);
      }
      &.active{
        > .accordion_header{
          ul{
            li{
              &:first-child{
