
.shadowbox{
  border-radius: 8px;
  background: #FFF;
  box-shadow: 3px 3px 20px 0px rgba(0, 0, 0, 0.06);
  width: 1782px;
  margin: 40px auto 16px auto;
  position: relative;
  .gnb{
    text-align: right;
    right: 76px;
    position: absolute;
    top:-30px;
    color: #262626;
    font-size: 16px;
    
    span{
      font-weight: 400;
      position: relative;
      padding: 0 12px 0 8px;
      font-family: "LG EI Text TTF";
      &::after{
        content: "";
        position: absolute;
        width: 6px;
        height: 6px;
        right:0px;
        top:9px;
        background-color: transparent;
        border-top: solid 1px #4d4d4e;
        border-right: solid 1px #4d4d4e;
        transform: translateY(-50%) rotate(-315deg);
      }
      &:last-child{
        font-weight: 600;
        padding-left: 3px;
        &::after{
          content: none;
        }
      }
    }
  }
  + .shadowbox{
    margin-top: 0;
  }
}
