/**/
.checkedinner > div{
  display: inline-flex;
  align-items: center;
}
.checkedinner input[type="checkbox"] {
  -webkit-appearance: none;
  position: relative;
  width: 18px;
  height: 18px;
  cursor: pointer;
  outline: none !important;
  border: 1px solid #eaeaea;
  border-radius: 5px;
  background: #fbfbfb;
}
.checkedinner label{
  padding-right: 10px;
  display: inline-block;
}
.checkedinner input[type="checkbox"]::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: hidden;
  width:20px;
  height:20px;
  transform: scale(0) translate(-50%, -50%);
  line-height: 1;
}

.checkedinner input[type="checkbox"]:checked {
  background-color: #EB1918;
  border-color: rgba(255, 255, 255, 0.3);
  color: white;
}

.checkedinner input[type="checkbox"]:checked::before, .checkedinner input[type="checkbox"]:checked::after {
  content: ''; 
  display: inline-block; 
  position: absolute; 
  transform-origin: left top; 
  background-color: #fff;
}

.checkedinner input[type="checkbox"]:checked::before {
  border: none;
  top: 8px; 
  left: 2px; 
  width: 2px;
  height: 7px; 
  transform: rotate(-45deg); 
}
.checkedinner input[type="checkbox"]:checked::after {
  top: 13px; 
  left: 8px; 
  width: 2px;
  height: 12px; 
  transform: rotate(-145deg); 
}
/**/
.radioinner div{
  padding-bottom: 20px;
  display: inline-flex;
  align-items: center;
}
.radioinner label {
  font-size: 18px;
  
  display: inline-flex;
  align-items: center;
}

.radioinner [type="radio"], span {
  vertical-align: middle;
}

.radioinner [type="radio"] {
  -webkit-appearance: none; 
  -moz-appearance: none; 
  appearance: none; 
  width: 20px;
  height: 20px;
  border: 3px solid white; 
  box-shadow: 0 0 0 1.6px #ccc; 
  border-radius: 50%;
  outline: none; 
  cursor: pointer;
}

.radioinner [type="radio"]:checked {
  background-color: #EB1918; 
  border: 3px solid white; 
  box-shadow: 0 0 0 1.6px #EB1918; 
}

.radioinner [type="radio"]:hover + span {
  cursor: pointer;
}

.radioinner [type="radio"]:disabled {
  background-color: #fff;
  box-shadow: none;
  opacity: 0.7;
  cursor: not-allowed;
  border: 3px solid white; 
  box-shadow: 0 0 0 1.6px #eee; 
}


/**/
.toggleinner > div{
  display: inline-flex;
  align-items: center;
}
.toggleinner label {
  display: inline-flex;
  align-items: center;
  cursor: pointer;
}

.toggleinner [type="checkbox"] {
  appearance: none;
  position: relative;
  /* border: max(2px, 0.1em) solid gray; */
  background-color: #D2CBC3;
  border-radius: 18px;
  height: 20px;
  width:34px;
}

.toggleinner [type="checkbox"]::before {
  content: "";
  position: absolute;
  left: 2px;
  width: 16px;
  height: 16px;
  top:2px;
  border-radius: 50%;
  transform: scale(0.8);
  background-color: #fff;
  transition: left 250ms linear;
}

.toggleinner [type="checkbox"]:checked {
  background-color: #EB1918;
  border-color: #EB1918;
  height: 20px;
  width:34px;
}

.toggleinner [type="checkbox"]:checked::before {
  background-color: white;
  left: 17px;
  top:2px
}

.toggleinner [type="checkbox"]:disabled {
  background-color: #F5F5F5;
  opacity: 0.7;
  cursor: not-allowed;
}

.toggleinner [type="checkbox"]:disabled:before {
  background-color: #fff;
}

.toggleinner [type="checkbox"]:disabled + span {
  opacity: 0.7;
  cursor: not-allowed;
  background-color: #F5F5F5;
}

.toggleinner [type="checkbox"]:focus-visible {
  outline-offset: max(2px, 0.1em);
  outline: max(2px, 0.1em) solid tomato;
}

